export default {
	data() {
		return {
			is_true: true,
			phone_number: "",
			//用户输入得验证码
			verification_code: null,
			verify: "获取验证码",
			//验证倒计时
			count_down: 60,
			//存储接收验证码
			verify_code: 123,
