		change() {
			this.is_true = !this.is_true
		},
		get_code() {
			var phone_reg = /^[1][3,4,5,7,8,9][0-9]{9}$/;
			if (!phone_reg.test(this.phone_number)) {
				uni.showToast({
					"title": "手机号错误，请重新填写",
					"icon": "none"
				});
				return;
			}
			let that = this;
			that.is_click = false;
			var timer = setInterval(function() {
				that.verify = that.count_down-- + " S";
				if (that.count_down < -1) {
					clearInterval(timer);
					that.verify = "获取验证码";
					that.count_down = 60;
					that.is_click = true;
					return;
				}
			}, 1000)
			uni.request({
				url: '',
				method: 'GET',
				data: {},
				success: res => {
					that.verify_code = res;
				},
			});
		},
		login() {
			var that = this;
			var phone_reg = /^[1][3,4,5,7,8,9][0-9]{9}$/;
			if (!phone_reg.test(that.phone_number)) {
				uni.showToast({
					"title": "手机号不正确，请重新输入",
					"icon": "none"
				});
				return;
			};
			if (that.is_true == false) {
				uni.showToast({
					"title": "请同意用户协议",
					"icon": "none"
				});
				return;
			}
			if (that.verification_code == that.verify_code) {
				wx.switchTab({
					url: '/pages/account/account',
					fail(res) {
						console.log(res)
					}
				})
			} else {
				uni.showToast({
					"title": "验证码不正确",
					"icon": "none"
				});
				return;
			};
		},
