										let gender=res.userInfo.gender;
										let avatar=res.userInfo.avatarUrl;
										let city=res.userInfo.city;
										wx.showLoading({
											title: '登录中...'
										});
										wx.reLaunch({
											"url":"../account/account?nickname="+nickname+"&gender="+gender+"&avatar="+avatar+"&city="+city
										})
									}
								});
						  }else{
